.dialogs {
    display: grid;
    grid-template-columns: 3fr 9fr;
    font-family: Georgia;
}
.dialogsItems {
    padding: 10px;
}
.dialog.active {
    color: white;
}
.dialog.active img {
    width: 30px;
    border-radius: 50%;
    padding: 10px 10px 0 0;

}
.dialog.active span {
    text-decoration: underline;
}
.messagesContent {
    padding: 5px;
}
.messages {
    padding: 15px;

}
.sendMessageBtn {
    background-color: #555;
    color: white;
    font-size: 16px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}
.sendMessageBtn:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 14px 40px 0 rgba(0,0,0,0.19);
}